import {
  moveEvent,
  endEvent,
  getPositionOffset,
  getClientPosition,
  isOutOfBrowser,
  judgeResizeType,
  getSize,
  setSize,
  calDistance,
} from './common';
import constant from '../config/constant';

export function handleStartEventForMove(event) {
  function _handleEndEventForMove(event) {
    document.removeEventListener(moveEvent, _handleMoveEventForMove, false); // 

    event.preventDefault();

    /* movingclicksetTimeoutmovingclick */
    setTimeout(() => {
      window.className = window.className.replace(/ ?moving/, '');
    }, 0);
  }

  function _handleMoveEventForMove(event) {
    /* window */
    if (isOutOfBrowser(event)) {
      return false;
    }

    const position = getClientPosition(event); // /
    /*  */
    const positionOffset = {
      x: position.x - startPoint.x + originPositionOffset.x,
      y: position.y - startPoint.y + originPositionOffset.y,
    };

    window.style.top = positionOffset.y + 'px'; // top
    window.style.left = positionOffset.x + 'px'; // left
    window.style.bottom = 'auto'; // auto
    window.style.right = 'auto'; // auto

    /* movingclick */
    if (
      calDistance({
        x1: position.x,
        y1: position.y,
        x2: startPoint.x,
        y2: startPoint.y,
      }) > constant.AVAILABLE_CLICK_MAX_MOVE_DISTANCE &&
      window.className.indexOf('moving') === -1
    ) {
      window.className += ' moving';
    }
  }

  const window = this.window;
  const startPoint = getClientPosition(event); // 

  /* MoveHandlerresizeresize */
  if (
    this.params.resizable &&
    this.isMoveHandlerEqualWindow &&
    judgeResizeType(startPoint, window) !== 'middle'
  ) {
    return;
  }

  /*  */
  let size = getSize(window);
  size = {
    width: parseInt(size.width),
    height: parseInt(size.height),
  };
  setSize(window, size.width, size.height);

  document.addEventListener(moveEvent, _handleMoveEventForMove, false); // 
  document.addEventListener(endEvent, _handleEndEventForMove);

  const originPositionOffset = getPositionOffset(window); // 

  event.preventDefault();
}
